@import 'animation.css';
@import '../fonts/iconfont.css';
@media screen and (min-width: 320px) {
  html {
    font-size: 14px;
  }
}
@media screen and (min-width: 360px) {
  html {
    font-size: 16px;
  }
}
@media screen and (min-width: 400px) {
  html {
    font-size: 18px;
  }
}
@media screen and (min-width: 440px) {
  html {
    font-size: 20px;
  }
}
@media screen and (min-width: 480px) {
  html {
    font-size: 22px;
  }
}
@media screen and (min-width: 640px) {
  html {
    font-size: 28px;
  }
}
html {
  font-size: 62.5%;
}
html,
body,
.wrap {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}
input {
  font-size: 1.4rem;
}
p {
  margin: 0;
}
h1,
h2,
h3,
h4,
h5 {
  font-weight: 400;
}
input,
label {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
input:checked,
label:checked {
  background: none;
}
button {
  font-size: 1.6rem;
  border: none;
  outline: none;
}
input {
  border: none;
  outline: none;
  resize: none;
  -webkit-appearance: none;
}
a {
  text-decoration: none;
}
.center,
.tc {
  text-align: center;
}
.tr {
  text-align: right;
}
.none {
  display: none;
}
.gray_1 {
  color: #666;
}
.gray_2 {
  color: #999;
}
.blue_1 {
  color: #4385f5;
}
.border_r {
  border-right: 1px solid #d9d9d9;
}
.border_b {
  border-bottom: 1px solid #d9d9d9;
}
.f12 {
  font-size: 1.2rem;
}
.f14 {
  font-size: 1.4rem;
}
.f16 {
  font-size: 1.6rem;
}
.f18 {
  font-size: 1.8rem;
}
.f20 {
  font-size: 2rem;
}
.f30 {
  font-size: 3rem;
}
.f40 {
  font-size: 4rem;
}
.f50 {
  font-size: 5rem;
}
.mt-15 {
  margin-top: 15px;
}
.p-10 {
  padding: 10px;
}
.p-15 {
  padding: 15px;
}
.flex {
  display: flex;
}
.flex-col {
  flex-direction: column;
}
.flex-jcenter {
  justify-content: center;
}
.flex-acenter {
  align-items: center;
}
.flex-justify {
  justify-content: space-between;
}
.flex-jend {
  justify-content: flex-end;
}
.flex-around {
  justify-content: space-around;
}
.flex-wrap {
  flex-wrap: wrap;
}
.link {
  color: #4385f5;
}
.gray {
  color: #999;
}
.mint-button--danger {
  background-color: #dd484e;
}
.mint-button--danger.is-plain {
  color: #dd484e;
  border-color: #dd484e;
}
.mint-button--primary {
  background-color: #4385f5;
}
.mint-button--primary.is-plain {
  color: #4385f5;
  border-color: #4385f5;
}
.mint-button--success {
  background-color: #78c75e;
  color: #fff;
}
.mint-button--success.is-plain {
  color: #78c75e;
  border-color: #78c75e;
}
.mint-checkbox-input:checked + .mint-checkbox-core,
.mint-radio-input:checked + .mint-radio-core {
  background-color: #4385f5;
  border-color: #4385f5;
}
.mint-checkbox-core::after {
  transform: none;
  border: 0;
}
.mint-checkbox-input:checked + .mint-checkbox-core:after {
  transform: none;
}
.mint-checkbox-input:checked + .mint-checkbox-core:before {
  color: #fff;
  font-family: iconfont;
  font-size: 1.7rem;
  content: "\e645";
  margin-left: 2px;
}
.wrap .mint-header {
  background-color: #3667b6;
  height: 50px;
  line-height: 50px;
  font-size: 1.6rem;
}
.wrap .mint-header h1 {
  font-size: 1.8rem;
  flex: 2;
}
.wrap .mint-button-text,
.wrap .mint-button-icon {
  vertical-align: middle;
}
.wrap {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: #fafafa;
}
.wrap > .main {
  flex: 1;
  width: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.wrap .mint-navbar .mint-tab-item .mint-tab-item-label {
  font-size: 1.6rem;
  color: #4385f5;
}
.wrap .mint-navbar .mint-tab-item.is-selected {
  border-bottom: 3px solid #4385f5;
  color: #4385f5;
}
.full {
  background-color: #fafafa;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.fixedbody {
  width: 100vw;
  position: fixed;
}
.bottom-bar {
  position: static;
}
.common-popup {
  width: 90vw;
}
.common-popup .close-btn {
  float: right;
  vertical-align: middle;
  margin-right: 10px;
  color: #999;
}
.common-popup .close-btn .iconfont {
  font-size: 2.2rem;
}
.common-popup .common-content {
  padding: 25px 0;
  border-bottom: 1px solid #d9d9d9;
  min-height: 36px;
  position: relative;
}
.help-bar {
  padding: 10px;
  font-size: 1.2rem;
}
.is-danger {
  color: #dd484e;
}
.flag-box {
  display: flex;
  position: relative;
  width: 100%;
}
.flag-box > span {
  flex: 1;
  margin: 0 5px;
}
.flag-box > span input {
  height: 30px;
  width: 100%;
  position: absolute;
  z-index: 2;
  opacity: 0;
}
.flag-box > span input:checked + b {
  border-color: #4385f5;
}
.flag-box > span input:checked + b:after {
  font-family: iconfont;
  font-size: 1.2rem;
  content: "\e645";
  position: absolute;
  right: 0;
  bottom: -8px;
  color: #fff;
  z-index: 4;
}
.flag-box > span input:checked + b:before {
  content: "";
  transform: rotate(45deg);
  position: absolute;
  right: -15px;
  bottom: -15px;
  background: #4385f5;
  width: 30px;
  height: 30px;
  z-index: 3;
}
.flag-box > span b {
  border: 1px solid #d9d9d9;
  display: inline-block;
  width: 100%;
  height: 30px;
  line-height: 30px;
  text-align: center;
  position: relative;
  z-index: 1;
  overflow: hidden;
  font-weight: normal;
}
.mint-cell .money {
  color: #333;
  margin-right: 12px;
  min-width: 82px;
  text-align: right;
}
.mint-cell .handle {
  min-width: 30vw;
  text-align: right;
  font-size: 1.2rem;
}
.mint-cell .handle .icon-qrcode {
  font-size: 3rem;
  color: #333;
}
.button-box {
  padding: 20px 15px 0;
}
.mint-cell {
  padding: 0 15px;
}
.mint-cell-wrapper {
  border-bottom: 1px solid #d9d9d9;
  background-size: 120% 0;
  padding: 0;
}
.border-b {
  border-bottom: 1px solid #d9d9d9;
}
.pop-box {
  width: 90%;
  padding-bottom: 15px;
  border-radius: 4px;
}
.pop-box .title {
  position: relative;
  padding: 10px 0;
  font-size: 1.6rem;
  color: #666;
}
.pop-box .title-right {
  position: absolute;
  top: 0;
  right: 10px;
  font-size: 30px;
  color: #999;
}
.input-box input {
  width: 100%;
  height: 46px;
  text-indent: 1em;
}
.input-tr input {
  text-align: right;
}
.mint-cell-wrapper button {
  padding: 0px 20px;
  height: 32px;
  font-size: 1.4rem;
}
.placeholder,
input::-webkit-input-placeholder {
  color: #999;
  font-size: 1.4rem;
}
.mask:before {
  content: '';
  z-index: 2;
  left: -200vw;
  top: -200vh;
  position: fixed;
  width: 500vw;
  height: 500vh;
  background: rgba(255, 255, 255, 0.15);
}
.select-wrap {
  display: flex;
  justify-content: center;
  line-height: 1.6;
  width: 100%;
}
.select-wrap select {
  background: none;
  border: 0;
  font-size: 1.6rem;
  line-height: 1.6;
  flex: 20;
}
.select-wrap .iconfont {
  flex: 1;
  color: #999;
  vertical-align: middle;
}



